table {
   border-collapse: collapse;
}
/* The Modal (background) */

.modal {
	display: none;
	/* Hidden by default */
	position: fixed;
	/* Stay in place */
	z-index: 1;
	/* Sit on top */
	padding-top: 100px;
	/* Location of the box */
	width: 50%;
	height: 50%;
	/* Fallback color */
	/* Black w/ opacity */
    margin: 0% 30%;
}


/* Modal Content */

.modal-content {
	position: relative;
	background-color: #fefefe;
	margin: auto;
	padding: 0;
	border: 1px solid #888;
	width: 80%;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	-webkit-animation-name: animatetop;
	-webkit-animation-duration: 0.4s;
	animation-name: animatetop;
	animation-duration: 0.4s
}


/* Add Animation */

@-webkit-keyframes animatetop {
	from {
		top: -300px;
		opacity: 0
	}
	to {
		top: 0;
		opacity: 1
	}
}

@keyframes animatetop {
	from {
		top: -300px;
		opacity: 0
	}
	to {
		top: 0;
		opacity: 1
	}
}


/* The Close Button */

.close {
	color: white;
	float: right;
	font-size: 28px;
	font-weight: bold;
}

.close:hover,
.close:focus {
	color: #000;
	text-decoration: none;
	cursor: pointer;
}

.modal-header {
	padding: 2px 16px;
	background-color:   var(--main-bg-color);
	color: white;
}

.modal-body {
    padding: 5% 0px;
    background-color: #eee;
}

.modal-footer {
	padding: 2px 16px;
	background-color: #5cb85c;
	color: white;
}

.btn-ok {
	background-color: yellowgreen;
	font-weight: bolder;
}
.btn-ok:hover {
	background-color: rgb(200, 233, 135);
	color: black;
}

.btn-cancel {
	background-color: lightcoral;
	font-weight: bolder;
}
.btn-cancel:hover {
	background-color: rgb(238, 174, 174);
	color: black;
}
.dropdown {
    position: relative;
    display: inline-block;
    width: 100%;
}

.dropdown:hover .dropdown-content {
    display: block;
}

.dropdown-content {
    display: none;
    position: absolute;
    width: 90%;
    z-index: 1;
	background-color: #222;
    color: white;
    padding: 5%;
    border-radius: 5%;
  }
  
#buttonSave {
	font-size: 15px;
    border-radius: 0%;
    margin-top: 5px;
}

h5 {
	text-align: center;
}

.range {
